const messagesReducer = (state=[], action) => {
  switch(action.type) {
    case 'SET_MESSAGES':
      return action.payload;
    default:
      return [
        {
          "author":"anonymous92",
          "content":"Hello world!",
          "created_at":"2017-09-26T23:03:16.365Z"
        },
        {
          "author":"anonymous77",
          "content":"My name is anonymous77",
          "created_at":"2017-09-26T23:03:21.194Z"
        }
      ];
  }
}

export default messagesReducer;
